                <el-breadcrumb-item></el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="text">
            <p class="title">【钧窑】</p>
            <p class="p_main">
              &emsp;&emsp;钧窑，即钧台窑，是在柴窑和鲁山花瓷的风格基础上综合而成的一种独特风格，受道家思想深刻影响，在宋徽宗时期达到高峰，其工艺技术发挥到极致。无论是呈色和各种纹理的表现，做到窑变可控随心所欲的表现技术，无人能仿造。宋代受理学的影响，反应在瓷器上就是规整对称，无论是造型或纹理都遵循这一原则，特别是北宋官造钧瓷，不论文房用具或大型祭器都严格遵守这一原则。规整对称，高雅大气，宫廷气势，一丝不苟。其势沉重古朴，明亮而深沉。建窑，耀州窑，钧窑，作为中国传统瓷文化正宗，历史悠久的风格传承。放大镜观察，蚯蚓走泥纹，用气泡一个个串连一体呈立体的爬行状，青蛙卵纹包裹卵核一摞摞正欲孵化而出。还有牛血纹，血色深红暗沉杀气腾腾，庄重威严等等，难以一一叙述。北宋官窑钧瓷的发色和纹理大小和分布，都难以做到人为控制而恰到好处，是北宋瓷艺的最高工艺水平的体现。
            </p>
            <div align="center" style="padding-top: 30px;">
              <el-carousel style="width: 60%;" :height="bannerHeight + 'px'">
                <el-carousel-item v-for="item in imgwrap" :key="item.url">
                  <el-image
                    style=" height: 100%;"
                    :src="item.url"
                    :preview-src-list="srcList"
                  ></el-image>
                </el-carousel-item>
              </el-carousel>
            </div>
            <div align="center" style="padding-top: 30px;">
              <i
                ref="off"
                class="el-icon-star-off"
                @click="change1"
                style="display: block"
              ></i>
              <i
                ref="on"
                class="el-icon-star-on"
                @click="change2"
                style="display: none"
              ></i>
            </div>
            <div align="center" style="padding-top: 10px;margin-bottom: 30px;">
              <span>{{ collectors }}</span>
            </div>
          </div>
        </el-main>
        <el-footer>Footer</el-footer>
